#preloader {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

#loader {
  width: 150px;
  height: 150px;
  border: 3px solid rgba(0, 0, 0, 0);
  border-top-color: #f7f7f7;
  border-radius: 50%;
  margin: -75px 0 0 -75px;
  animation: spin 2s linear infinite;
  display: block;
  position: relative;
  top: 50%;
  left: 50%;
}

#loader:before {
  content: "";
  border: 3px solid rgba(0, 0, 0, 0);
  border-top-color: #ff6b01;
  border-radius: 50%;
  animation: spin 3s linear infinite;
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
}

#loader:after {
  content: "";
  border: 3px solid rgba(0, 0, 0, 0);
  border-top-color: #818181;
  border-radius: 50%;
  animation: spin 1.5s linear infinite;
  position: absolute;
  top: 15px;
  bottom: 15px;
  left: 15px;
  right: 15px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

