.backdrop {
  z-index: 10;
  height: 100vh;
  width: 100vw;
  transition: opacity 300ms var(--cubic-bezier);
  background-color: rgba(0, 0, 0, .3);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.backdrop.visually-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal {
  width: 280px;
  min-height: 300px;
  transition: transform 1000ms var(--cubic-bezier);
  background-color: #fff;
  padding: 50px 20px 40px;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.backdrop.is-hidden .modal {
  transform: translateX(-50%);
}

